function wugniu2IPA(wugniu) {
  let result = wugniu;
  let pairs = [
    [/er/, "l"],
    [/^n(.)$/, "n$1"],
    [/^m(.)$/, "m$1"],
    [/^ng(.)$/, "$1"],
    [/y(.)$/, "z$1"],
    [/^sh/, ""],
    [/([ptskc])h/, "$1"],
    [/^gh/, ""],
    [/^gn/, "i"],
    [/^ng/, ""],
    [/^zh/, ""],
    [/^c/, "t"],
    [/^j/, "d"],
    [/^y/, "i"],
    [/^w/, "u"],
    [/aon/, ""],
    [/ioe/, "y"],
    [/iuq/, "y"],
    [/iu/, "y"],
    [/au/, ""],
    [/eu/, ""],
    [/oe/, ""],
    [/an/, "a"],
    [/en/, "n"],
    [/on/, "o"],
    [/eq/, ""],
    [/iq/, "i"],
    [/q/, ""],
    [/ii/, "i"],
    [/iy/, "y"],
    [/uu/, "u"],
    [/1$/, ""],
    [/5$/, ""],
    [/6$/, ""],
    [/7$/, ""],
    [/8$/, ""],
  ];
  for (let [zie, gheu] of pairs) {
    result = result.replace(zie, gheu);
  }
  return "/" + result + "/";
}

